/* Custom CSS for the blog */

/* Hidden login button */
.login-trigger-area {
  position: absolute;
  bottom: 5px;
  right: 5px;
  width: 10px;
  height: 10px;
  cursor: pointer;
  opacity: 0.05;
  z-index: 1000;
}

.login-trigger-area:hover {
  opacity: 0.1;
}

/* Modal login form */
.login-modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1001;
}

.login-form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--body-bg);
  padding: 20px;
  border-radius: 5px;
  width: 300px;
}

.login-form h2 {
  margin-top: 0;
  color: var(--heading-color);
}

.login-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid var(--border-color);
  border-radius: 3px;
  background-color: var(--input-bg);
  color: var(--text-color);
}

.login-form button {
  width: 100%;
  padding: 10px;
  background-color: var(--btn-bg);
  color: var(--btn-text);
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.login-form button:hover {
  background-color: var(--btn-hover-bg);
}

.login-form .close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 20px;
  color: var(--text-color);
}

/* Admin panel */
.admin-panel {
  display: none;
  padding: 20px;
  background-color: var(--body-bg);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  margin-top: 20px;
}

.admin-panel h2 {
  margin-top: 0;
  color: var(--heading-color);
}

.admin-panel .tab-nav {
  display: flex;
  margin-bottom: 15px;
  border-bottom: 1px solid var(--border-color);
}

.admin-panel .tab-btn {
  padding: 10px 15px;
  background: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  color: var(--text-color);
}

.admin-panel .tab-btn.active {
  border-bottom: 2px solid var(--btn-bg);
  font-weight: bold;
}

.admin-panel .tab-content {
  display: none;
}

.admin-panel .tab-content.active {
  display: block;
}

.admin-panel textarea, .admin-panel input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid var(--border-color);
  border-radius: 3px;
  background-color: var(--input-bg);
  color: var(--text-color);
}

.admin-panel textarea {
  min-height: 300px;
  font-family: monospace;
}

.admin-panel button {
  padding: 8px 15px;
  background-color: var(--btn-bg);
  color: var(--btn-text);
  border: none;
  border-radius: 3px;
  cursor: pointer;
  margin-right: 10px;
}

.admin-panel button:hover {
  background-color: var(--btn-hover-bg);
}

.admin-panel .post-item {
  padding: 10px;
  border: 1px solid var(--border-color);
  border-radius: 3px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.admin-panel .post-item-title {
  font-weight: bold;
}

.admin-panel .post-item-actions button {
  padding: 5px 8px;
  margin-left: 5px;
}

.admin-panel .tag-item {
  display: inline-block;
  padding: 5px 10px;
  background-color: var(--tag-bg);
  color: var(--tag-text);
  border-radius: 15px;
  margin-right: 5px;
  margin-bottom: 5px;
}

/* Hide admin features by default */
.admin-only {
  display: none;
}